@import "./src/assets/sass/prepends.scss";

.card-wrapper {
  padding: 1rem 0.5rem;

  .card-photo {
    display: inline-block;
    position: relative;
    border-radius: 3px;
    color: #fff;
    transition: 0.4s;

    &:hover {
      text-decoration: none;
      color: #000;
      &::after {
        background-color: rgba(255, 255, 255, 1);
        box-shadow: inset 0 0 0px 5px rgba(0, 0, 0, 0.8);
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 3px;
      background-color: rgba(0, 0, 0, 0.8);
      box-shadow: inset 0 0 0px 5px rgba(255, 255, 255, 1);
      opacity: 0.3;
      transition: background-color 0.4s;
      z-index: 1;
    }

    img {
      position: relative;
      border-radius: 3px;
    }
    .photo-title,
    .photo-date {
      position: absolute;
      z-index: 2;
      max-width: calc(100% - 10px - 1.5rem);
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .photo-title {
      top: 0.75rem;
      left: 0.75rem;
    }
    .photo-date {
      bottom: 0.75rem;
      right: 0.75rem;
      font-size: 0.75rem;
    }
  }
}
